body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  direction: rtl;
  font-family: iransans
}

@font-face {
  font-family: iransans;
  src: url(/static/media/IRANSansWeb.50f4bba2.woff);
}

* {
  direction: rtl
}
.navbar {
  color: white
}

.navbar:hover{
  color: white;
  text-decoration: none
}
.landing_background {
  position: relative;
  background: url(/static/media/landing.ea2abcea.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.dark_overlay {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.landing_area {
  margin-top: 100px
}

.lead-landing {
  color: white
}
.form-control::-webkit-input-placeholder {
  color: lightgray
}
.form-control::-ms-input-placeholder {
  color: lightgray
}
.form-control::placeholder {
  color: lightgray
}
